},

methods: { // METHODS TO TOGGLE THE DATA PROPERTIES TO TRUE OR FALSE
    foodanddrink() {
      this.isfoodanddrink = true;
      this.isshopping = false;
      this.isdutyfree =false;
    },
    shopping() {
      this.isshopping = true;
      this.isfoodanddrink = false;
      this.isdutyfree =false;
    },
    dutyfree() { 
      this.isfoodanddrink = false;
      this.isshopping = false;
      this.isdutyfree = true;
    },
    
 },
}
</script>
